:global .nav .dropdown-toggle.btn {
  background-color: inherit !important;
  color: inherit !important;
  border: none !important;
}
@media (min-width: 769px) {
  :global .nav .dropdown-toggle.btn {
    height: 100%;
  }
}
:global .nav .dropdown-toggle.btn:active,
:global .nav .dropdown-toggle.btn:focus {
  box-shadow: none;
}
:global .nav-masthead .nav-link {
  padding: 0.25rem 0;
  opacity: 0.5;
  border-bottom: 2px solid transparent;
}
:global .nav-masthead .nav-link:hover,
:global .nav-masthead .nav-link:focus,
:global .nav-masthead .nav-link.active {
  opacity: 1;
  border-bottom-color: inherit;
}
:global .nav-masthead .nav-link + .nav-link {
  margin-left: 1rem;
}
:global .nav-scroller {
  position: relative;
  z-index: 2;
  height: 2.75rem;
  overflow-y: hidden;
}
:global .nav-scroller .nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  padding-bottom: 1rem;
  margin-top: -1px;
  overflow-x: auto;
  color: rgba(255, 255, 255, 0.75);
  text-align: center;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}
:global .nav-underline .nav-link {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-size: 0.875rem;
  color: #6c757d;
}
:global .nav-underline .nav-link:hover {
  color: #007bff;
}
:global .nav-underline .active {
  font-weight: 500;
  color: #343a40;
}
