:global .px-0 {
  padding-left: 0;
  padding-right: 0;
}
@media (max-width: 576px) {
  :global .navbar-expand-sm .navbar-nav > drop-menu > .dropdown-toggle {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (max-width: 768px) {
  :global .navbar-expand-md .navbar-nav > drop-menu > .dropdown-toggle {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (max-width: 992px) {
  :global .navbar-expand-lg .navbar-nav > drop-menu > .dropdown-toggle {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (max-width: 1200px) {
  :global .navbar-expand-xl .navbar-nav > drop-menu > .dropdown-toggle {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (max-width: 991.98px) {
  :global .offcanvas-collapse {
    position: fixed;
    bottom: 0;
    left: 100%;
    width: 100%;
    padding-right: 1rem;
    padding-left: 1rem;
    overflow-y: auto;
    visibility: hidden;
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
  :global .offcanvas-collapse.open {
    visibility: visible;
    transform: translateX(-100%);
  }
}
