.box {
  display: block;
  text-decoration: none !important;
  appearance: none;
  border: none;
  background: transparent;
  padding: 0.25rem;
  position: relative;
}
.box > span {
  width: 2rem;
  height: 2rem;
}
.box .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: white;
}
.box > img {
  border: 1px solid lightgray;
  display: none;
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
}
.box:hover > img {
  display: block;
}
